import React, { Component } from "react";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Modal, Radio, Col, Divider, InputNumber, Checkbox } from "antd";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class ModalUpdateItemLocation extends Component {
  state = {
    check_fixed_price:false,
    limit: 0,
  }

  componentDidUpdate(prevProps) {
    const { item_location_data } = this.props;
    const { setFieldsValue } = this.props.form;

    if (prevProps.item_location_data !== item_location_data) {
      let id_rack = [];

      if(item_location_data.racks !== undefined){
        item_location_data.racks.map((data) => {
          id_rack.push(data.id_rack)
        })
      }
    
      setFieldsValue({
        id_location : item_location_data.id_location,
        id_supplier_preferred : item_location_data.id_supplier_preferred,
        fixed_price_sales : item_location_data.fixed_price_sales,
        is_fixed_price : item_location_data.is_fixed_price,
        margin_in : item_location_data.margin ? item_location_data.margin.slice(-1) === "%" ? 0 : 1 : 1,
        margin : parseFloat(item_location_data.margin),
        id_purchase_unit : item_location_data.id_purchase_unit,
        id_sales_unit : item_location_data.id_sales_unit,
        id_purchase_tax : item_location_data.id_purchase_tax,
        id_sales_tax : item_location_data.id_sales_tax,
        purchase_qty : item_location_data.purchase_qty,
        min_stock : item_location_data.min_stock,
        max_stock : item_location_data.max_stock,
        reorder_point : item_location_data.reorder_point,
        id_rack
      });

      this.setState({
        check_fixed_price : item_location_data.is_fixed_price === 1 ? true : false
      })
    } 
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        if(values.margin_in === 0 && (values.is_fixed_price === false || values.is_fixed_price=== 0)){
          values.margin = values.margin + "%"
        }
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { item } = this.props;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="updateItemLocation" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="60%"
        okText={<IntlMessages id="button.submit"></IntlMessages>}
        cancelText={<IntlMessages id="button.cancel"></IntlMessages>}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <InputGroup>
            <Col span={1} />
            <Col span={10}>
            <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="locations.pageHeader" />}
              >
                {getFieldDecorator("id_location", {
                  //initialValue: data.id_location,
                })(
                  <Select
                    loading={item.loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width:"100%" }}
                    disabled={true}
                  >
                    {item.locations.map((location, key) => (
                    
                      <Option key={key} value={location.id_location}>
                        { location.location_code + " ("+location.location_name +")"}
                      </Option>
                          
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={2} />
            <Col span={10}>
              {
                item.item !== null && (item.item.item_type === 3 || item.item.item_type === 4)
                ? null
                : 
                  (
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="items.preferredSupplier" />}
                    >
                      {getFieldDecorator("id_supplier_preferred", {
                        //initialValue: data.id_supplier_preferred,
                      })(
                        <Select
                          loading={this.props.item.loading}
                          showSearch
                          optionFilterProp="children"
                          style={{ width:"100%" }}
                        >
                          {item.suppliers.map((supplier, key) => (
                            supplier.id_location === getFieldValue("id_location")
                            ? 
                              (
                                <Option key={key} value={supplier.id_supplier}>
                                  { supplier.supplier_code + " ("+supplier.supplier_name +")"}
                                </Option>
                              )
                            : null   
                          ))}
                        </Select>
                      )}
                    </Form.Item>
                  )
              }
              
            </Col>
            <Col span={1} />
          </InputGroup>

          <Divider 
            ////orientation="left" 
            style={{ fontSize: 14 }}
          >
            <IntlMessages id="salesPurchaseItemLocation" />
          </Divider>

          <InputGroup>
            <Col span={1} />
            <Col span={10}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.purchaseUnit" />}
              >
                {getFieldDecorator("id_purchase_unit", {
                  //initialValue: data.id_purchase_unit,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.item.loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width: "100%" }}
                  >
                    {item.units.map((data, key) => (
                      item.item !== null 
                      ?
                        item.item.array_unit_conversion.filter((value)=>value===data.id_unit).length > 0
                          ? 
                          (<Option key={key} value={data.id_unit}>
                            { data.unit_code +" ("+data.unit_name+")"}
                          </Option>)
                        : null
                      : null
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={2} />
            {
              item.item !== null && (item.item.item_type !== 2)
                ? 
                  (
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.salesUnit" />}
                      >
                        {getFieldDecorator("id_sales_unit", {
                          //initialValue: data.id_sales_unit,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            loading={this.props.item.loading}
                            showSearch
                            optionFilterProp="children"
                            style={{ width: "100%" }}
                          >
                            {item.units.map((data, key) => (
                              item.item !== null 
                              ?
                                item.item.array_unit_conversion.filter((value)=>value===data.id_unit).length > 0
                                  ? 
                                  (<Option key={key} value={data.id_unit}>
                                    { data.unit_code +" ("+data.unit_name+")"}
                                  </Option>)
                                : null
                              : null
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  )
                : null
            }
            
            <Col span={1} />
          </InputGroup>

          <InputGroup>
            <Col span={1} />
            <Col span={10}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.purchaseTax" />}
              >
                {getFieldDecorator("id_purchase_tax", {
                  //initialValue: data.id_purchase_tax,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={item.loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width: "100%" }}
                  >
                    {item.taxes.map((data, key) => (
                      <Option key={key} value={data.id_tax}>
                        { data.tax_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={2} />
            {
              item.item !== null && (item.item.item_type !== 2)
                ? 
                  (
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.salesTax" />}
                      >
                        {getFieldDecorator("id_sales_tax", {
                          //initialValue: data.id_sales_tax,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            loading={item.loading}
                            showSearch
                            optionFilterProp="children"
                            style={{ width: "100%" }}
                          >
                            {item.taxes.map((data, key) => (
                              <Option key={key} value={data.id_tax}>
                                { data.tax_name}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  )
                : null
            }
            <Col span={1} />
          </InputGroup>

          <InputGroup>
            <Col span={1} />
            <Col span={10}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.purchaseQuantity" />}
              >
                {getFieldDecorator("purchase_qty", {
                  //initialValue: data.purchase_qty,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <InputNumber
                    formatter={value =>
                      `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    }
                    parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    min={0.01}
                    precision={2}
                    style={{ width: "100%" }}
                  />
                )}
              </Form.Item>
            </Col>
            <Col span={2} />
            <Col span={10}>
              
            </Col>
            <Col span={1} />
          </InputGroup>

          {
            item.item !== null && (item.item.item_type !== 1)
            ? null
            : 
              (
                <>
                  <Divider 
                    //orientation="left" 
                    style={{ fontSize: 14 }}
                  >
                    <IntlMessages id="inventoryItemLocation" />
                  </Divider>
                  
                  <InputGroup>
                    <Col span={1} />
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.minStock" />}
                      >
                        {getFieldDecorator("min_stock", {
                          // initialValue: data.min_stock,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <InputNumber
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            min={0}
                            precision={2}
                            style={{ width: "100%" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={2} />
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.maxStock" />}
                      >
                        {getFieldDecorator("max_stock", {
                          // initialValue: data.max_stock,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <InputNumber
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            min={0}
                            precision={2}
                            style={{ width: "100%" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>  
                    
                  <InputGroup>
                    <Col span={1} />
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.reorderPoint" />}
                      >
                        {getFieldDecorator("reorder_point", {
                          // initialValue: data.reorder_point,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <InputNumber
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            min={0}
                            precision={2}
                            style={{ width: "100%" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={2} />
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.rack" />}
                      >
                        {getFieldDecorator("id_rack", {
                          // initialValue: data.id_supplier_preferred,
                        })(
                          <Select
                            mode="multiple"
                            loading={this.props.item.loading}
                            showSearch
                            optionFilterProp="children"
                            style={{ width: "100%" }}
                          >
                            { item.racks.map((rack, key) => (
                              rack.id_location === getFieldValue("id_location")
                                ? 
                                  (<Option key={key} value={rack.id_rack}>
                                    { rack.rack_code + " (" + rack.rack_name +")"}
                                  </Option>)
                                : null
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={1} />
                  </InputGroup>  
                </>  
              )
          }
    
        </Form>
      </Modal>
    );
  }
}

const WrappedModalUpdateItemLocation = Form.create({ name: "modal_update_item_location" })(
  ModalUpdateItemLocation
);

export default WrappedModalUpdateItemLocation;
