import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import ModalAddItemLocation from "./modalAddItemLocation";
import ModalUpdateItemLocation from "./modalUpdateItemLocation";

import { Tooltip, Col } from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
// import LayoutWrapper from "../../../../components/utility/layoutWrapper";
// import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/item/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
//import { convertDiscountMask } from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange"

// const Panel = Collapse.Panel;
// const Option = Select.Option;
// const Search = Input.Search;

class ItemLocation extends Component {

  state = {
    visibleAdd: false,
    visibleUpdate: false,
    item_location_data: null,
  }


  handleModalAdd = () => {
    this.setState({
      visibleAdd: !this.state.visibleAdd
    })
  }

  handleModalUpdate = row => {
    this.state.visibleUpdate
      ? this.setState({ visibleUpdate: false, item_location_data: {} })
      : this.setState({ visibleUpdate: true, item_location_data: row });
  }

  listAction = {
    title: "",
    dataIndex: "id_location",
    rowKey: "id_location",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <React.Fragment>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Button
            size="small"
            className="invoiceEdtBtn"
            onClick={() => this.handleModalUpdate(row)}
          >
            <i className="ion-android-create" />
          </Button>
        </Tooltip>
      </React.Fragment>
    )
  };

  listActionNon = {
    title: "",
    dataIndex: "id_location",
    rowKey: "id_location",
    align: "center",
    render: (text, row) => (
      <React.Fragment>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Button
            size="small"
            className="invoiceEdtBtn"
            onClick={() => this.handleModalUpdate(row)}
          >
            <i className="ion-android-create" />
          </Button>
        </Tooltip>
      </React.Fragment>
    )
  };

  render() {

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        width: "20%",
        fixed: "left",
        render: text => <div style={{ padding: "0 12px" }}>{text}</div>
      },
      {
        title: getLanguage({ id: "items.purchaseUnit" }),
        dataIndex: "purchase_unit.unit_code",
        rowKey: "purchase_unit.unit_code",
        width: "10%",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.salesUnit" }),
        dataIndex: "sale_unit.unit_code",
        rowKey: "sale_unit.unit_code",
        width: "10%",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.purchaseTax" }),
        dataIndex: "purchase_tax.tax_name",
        rowKey: "purchase_tax.tax_name",
        width: "10%",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.salesTax" }),
        dataIndex: "sale_tax.tax_name",
        rowKey: "sale_tax.tax_name",
        width: "10%",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.purchaseQuantity" }),
        dataIndex: "purchase_qty",
        rowKey: "purchase_qty",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />

      },
      {
        title: getLanguage({ id: "items.minStock" }),
        dataIndex: "min_stock",
        rowKey: "min_stock",
        align: "right",
        width: "10%",
        render: text => {
          return <NumberFormat value={parseFloat(text)} />
        }
      },
      {
        title: getLanguage({ id: "items.maxStock" }),
        dataIndex: "max_stock",
        rowKey: "max_stock",
        align: "right",
        width: "10%",
        render: text => {
          return <NumberFormat value={parseFloat(text)} />
        }
      },
      {
        title: getLanguage({ id: "items.reorderPoint" }),
        dataIndex: "reorder_point",
        rowKey: "reorder_point",
        align: "right",
        width: "10%",
        render: text => {
          return <NumberFormat value={parseFloat(text)} />
        }
      },

    ];

    const columnsForNonInventory = [
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        //fixed: "left",  
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.purchaseUnit" }),
        dataIndex: "purchase_unit.unit_code",
        rowKey: "purchase_unit.unit_code",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.purchaseTax" }),
        dataIndex: "purchase_tax.tax_name",
        rowKey: "purchase_tax.tax_name",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.purchaseQuantity" }),
        dataIndex: "purchase_qty",
        rowKey: "purchase_qty",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />

      },
    ]

    const columnsForGroupService = [
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        //width: "20%",
        fixed: "left",
        render: text => <div style={{ padding: "0 12px" }}>{text}</div>
      },
      {
        title: getLanguage({ id: "items.purchaseUnit" }),
        dataIndex: "purchase_unit.unit_code",
        rowKey: "purchase_unit.unit_code",
        width: "25%",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.salesUnit" }),
        dataIndex: "sale_unit.unit_code",
        rowKey: "sale_unit.unit_code",
        width: "25%",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.purchaseTax" }),
        dataIndex: "purchase_tax.tax_name",
        rowKey: "purchase_tax.tax_name",
        width: "20%",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.salesTax" }),
        dataIndex: "sale_tax.tax_name",
        rowKey: "sale_tax.tax_name",
        width: "20%",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.purchaseQuantity" }),
        dataIndex: "purchase_qty",
        rowKey: "purchase_qty",
        width: "15%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />

      },
    ];
    //console.log(this.props.item);
    // console.log(this.state.filter);

    // Data setup
    const { item, loading } = this.props.item;
    //console.log(item)
    const item_locations = item !== null ? item.item_locations : [];
    //console.log(this.props.item); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (
      permissions.includes("Update Item") &&
      columns.length === 9 &&
      columnsForGroupService.length === 6 &&
      columnsForNonInventory.length === 4
    ) {
      //console.log("Ya")
      columns.push(this.listAction);
      columnsForGroupService.push(this.listActionNon);
      columnsForNonInventory.push(this.listActionNon);
    }

    Object.keys(item_locations).map((item_location, index) => {
      return dataSource.push({
        ...item_locations[item_location],
        key: item_location
      });
    });


    return (
      <>
        <ModalAddItemLocation
          visible={this.state.visibleAdd}
          handleModal={this.handleModalAdd}
          submit={this.props.handleSubmitAddItemLocation}
          item={this.props.item}
        />
        <ModalUpdateItemLocation
          visible={this.state.visibleUpdate}
          item_location_data={this.state.item_location_data}
          handleModal={this.handleModalUpdate}
          submit={this.props.handleSubmitUpdateItemLocation}
          item={this.props.item}
        />

        <div
          style={{ marginRight: "20px", float: "right" }}
        >
          <Button
            type="primary"
            onClick={this.handleModalAdd.bind(this)}
          >
            <IntlMessages id="button.addItemLocation" />
          </Button>
        </div>

        <CardWrapper>
          <Col span={24} style={{ marginTop: "20px", padding: "0 20px 0 20px" }}>
            <div className="isoInvoiceTable">
              <Scrollbars style={{ width: "100%" }}>
                <TableWrapper
                  // size="small"
                  dataSource={dataSource}
                  columns={
                    item && (item.item_type === 3 || item.item_type === 4)
                      ? columnsForGroupService
                      : item && item.item_type === 2
                        ? columnsForNonInventory
                        : columns
                  }
                  loading={loading}
                  className="invoiceListTable"
                  bordered={true}
                  pagination={true}
                />
              </Scrollbars>
            </div>
          </Col>
        </CardWrapper>
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    item: state.item,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(ItemLocation);
