import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Icon, Row, Col, Alert, Select, TreeSelect, Checkbox, Tooltip, InputNumber, Divider, Radio } from "antd";
import actions from "../../../../redux/item/item/actions";
import Popconfirms from "../../../../components/feedback/popconfirm";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

const radioGroupStyle = {
  display: "flex",
  width: "100%"
}

const radioButtonStyle = {
  flex: "1",
  textAlign: "center"
}


let id = 0;

class ItemUpdateDescForm extends Component {
  state = {
    id_base_unit: null,
    limit: 0,
  }

  // componentDidMount() {
  //   const { editItem } = this.props;
  //   const id  = this.props.id_item;
  //   editItem(id);
  // }

  componentDidUpdate(prevProps) {

    if (prevProps.item !== this.props.item && this.props.item.item !== null) {
      // console.log(this.props.item.item);
      let unit_conversion = this.props.item.item.unit_conversion;

      this.setState({
        // id_location: this.props.item.item.locations,
        limit: 1,
        id_base_unit: this.props.item.item.id_base_unit,
      })
      const { form } = this.props
      // const keys = form.getFieldValue('keys');
      let array = [];

      unit_conversion.map((data, index) => {
        array.push(index);
      })
      form.setFieldsValue({
        keys: array,
      });
      id = 0
      id = unit_conversion.length
    }
  }

  removeUnit = (k) => {
    const { form } = this.props;
    // can use data-binding to get
    const keys = form.getFieldValue('keys');
    // We need at least one passenger
    // if (keys.length === 1) {
    //   return;
    // }

    // can use data-binding to set
    form.setFieldsValue({
      keys: keys.filter(key => key !== k),
    });
  }

  addUnit = () => {
    const { form } = this.props;
    // can use data-binding to get
    const keys = form.getFieldValue('keys');
    if (keys.length < 5) {
      const nextKeys = keys.concat(id++);
      // can use data-binding to set
      // important! notify form to detect changes
      form.setFieldsValue({
        keys: nextKeys,
      });
    }

  }


  handleChangeBaseUnit = (e) => {
    const { setFieldsValue, getFieldValue } = this.props.form
    let id_unit = getFieldValue('id_unit')

    this.setState({
      id_base_unit: e,
    })

    if (id_unit) {
      id_unit.map((data, index) => {
        if (data === e) {
          id_unit[index] = null
        }
      })
    }

    setFieldsValue({
      id_base_unit: e,
      id_unit
    })
  }

  handleChangeItemLocation = (e) => {
    //console.log(e);
    if (this.state.id_location_error !== null) {
      this.setState({
        id_location_error: null
      })
    }

    if (e.includes("all")) {
      console.log("");
      this.setState({
        id_location: this.props.item.array_id_location
      })
    }
    else {
      if (e.length === 0) {
        this.setState({
          id_location_error: "Required"
        })
      }
      console.log("");
      this.setState({
        id_location: e
      })
    }
  }

  handleChangeBaseUnitFixedPrice = (e) => {
    const { setFieldsValue } = this.props.form
    if (e === false) {
      setFieldsValue({
        ["base_unit_margin"]: 0,
        ["base_unit_is_fixed_price"]: e
      })
    }
    else {
      setFieldsValue({
        ["base_unit_fixed_price_sales"]: 0,
        ["base_unit_is_fixed_price"]: e
      })
    }

  }

  handleChangeFixedPrice = (index, e) => {
    const { setFieldsValue } = this.props.form
    if (e === false) {
      setFieldsValue({
        [`margin[${index}]`]: 0,
        [`is_fixed_price[${index}]`]: e
      })
    }
    else {
      setFieldsValue({
        [`fixed_price_sales[${index}]`]: 0,
        [`is_fixed_price[${index}]`]: e
      })
    }

  }

  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { form } = this.props;
      const keys = form.getFieldValue('keys');
      let unit_conversions = [];

      // console.log(keys.length)

      if (keys.length > 1) {
        //console.log(values)
        values.id_unit.map((data, index) => {
          //console.log(index)
          unit_conversions.push({
            id_unit: values.id_unit[index],
            unit_conversion: values.conversion[index],
            is_fixed_price: values.is_fixed_price ? values.is_fixed_price[index] : false,
            fixed_price_sales: values.fixed_price_sales ? values.fixed_price_sales[index] : 0,
            margin: values.margin ? values.margin_in[index] === 1 ? values.margin[index] : `${values.margin[index]}%` : 0,
            weight: values.weight ? values.weight[index] : 0,
            id_unit_weight: values.id_unit_weight ? values.id_unit_weight[index] : null,
            measure_by: values.measure_by[index],
            volume: values.volume ? values.volume[index] : 0,
            id_unit_volume: values.id_unit_volume ? values.id_unit_volume[index] : null,
            length: values.length ? values.length[index] : 0,
            width: values.width ? values.width[index] : 0,
            height: values.height ? values.height[index] : 0,
            id_unit_dimension: values.id_unit_dimension ? values.id_unit_dimension[index] : null,
          })
        })
      }

      values.unit_conversions = unit_conversions

      // console.log(values);
      if (!err) {
        const { updateItem } = this.props;

        if (values.item_type !== 2) {
          values.base_unit_margin = values.base_unit_margin_in === 1 ? values.base_unit_margin : `${values.base_unit_margin}%`
        }
        updateItem(this.props.item.item.id_item, values);
        //this.props.submit(values);
      }
    });
  }

  render() {
    // console.log(id);
    // console.log(this.props.item.item);
    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;
    const { item } = this.props;
    const { loading, loadingSubmit, item_categories } = item;
    const data = item.item ? item.item : [];

    const used = data.is_used;
    const error = item.errorField ? item.errorField : [];
    // console.log(data);

    let unit_selected = getFieldValue('id_unit') ? getFieldValue('id_unit') : [];

    const loop = array =>
      array.map(data => {
        let perData = {
          title: data.item_category_code + " (" + data.item_category_name + ")",
          value: data.id_item_category,
          key: data.id_item_category,
          children: data.children_active ? loop(data.children_active) : null
        }

        return perData
      })

    getFieldDecorator('keys', { initialValue: [] });
    const keys = getFieldValue('keys');
    const formItems = keys.map((k, index) => (
      //console.log(data.unit_conversion[k])
      data.unit_conversion[k] === undefined || data.unit_conversion[k].id_unit !== data.id_base_unit ?
        (
          <div
            key={k}
          >
            <Divider />
            <InputGroup
            // style={{ 
            //   paddingLeft: "50px",
            //   paddingRight: "50px"
            // }}          
            >
              <Col span={9}>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="items.unit" values={{ index: index + 1 }} />}
                  >
                    {getFieldDecorator(`id_unit[${k}]`, {
                      initialValue: data.unit_conversion[k] ? data.unit_conversion[k].id_unit : null,
                      rules: [{
                        required: true,
                        //whitespace: true,
                        message: <IntlMessages id="required" />,
                      }],
                    })(
                      <Select
                        loading={this.props.item.loading}
                        showSearch
                        style={{ width: "95%" }}
                        //onChange={this.handleChangeBaseUnit}
                        optionFilterProp="children"
                        disabled={
                          data.unit_conversion[k]
                            ? data.unit_conversion[k].is_used
                              ? true
                              : false
                            : false
                        }
                      >
                        {this.props.item.units.map((data, key) => (
                          // unit_selected.findIndex((x) => x === data.id_unit) === -1 ?
                          getFieldValue(`id_unit[${k}]`) === data.id_unit ?
                            (<Option key={key} value={data.id_unit}>
                              {data.unit_code + " (" + data.unit_name + ")"}
                            </Option>)
                            : null
                        ))}

                        {this.props.item.units.map((data, key) => (
                          Array.isArray(unit_selected) && unit_selected.findIndex((x) => x === data.id_unit) === -1 && this.state.id_base_unit !== data.id_unit ?
                            (<Option key={key} value={data.id_unit}>
                              {data.unit_code + " (" + data.unit_name + ")"}
                            </Option>)
                            : null
                        ))}
                      </Select>
                    )}

                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="items.conversion" />}
                  >
                    {getFieldDecorator(`conversion[${k}]`, {
                      initialValue: data.unit_conversion[k] ? data.unit_conversion[k].unit_conversion : null,
                      rules: [{
                        required: true,
                        message: <IntlMessages id="required" />,
                      }],
                    })(
                      <InputNumber
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={0.01}
                        precision={2}
                        style={{ width: "100%" }}
                        disabled={
                          data.unit_conversion[k]
                            ? data.unit_conversion[k].is_used
                              ? true
                              : false
                            : false
                        }
                        style={{ width: '100%' }}
                      />
                    )}
                  </Form.Item>
                </Col>
              </Col>

              {getFieldValue('item_type') !== 2
                ?
                (
                  <Col span={15}>
                    <Col span={1} />
                    <Col span={10}>
                      <InputGroup compact>
                        <Col span={8}>
                          <Form.Item
                            {...formItemLayout}
                            label={<IntlMessages id="isFixedPrice" />}
                          >
                            {getFieldDecorator(`is_fixed_price[${k}]`, {
                              valuePropName: 'checked',
                              initialValue: data.unit_conversion[k] ? data.unit_conversion[k].is_fixed_price === 1 ? true : false : false,
                            })(
                              <Checkbox
                                style={{
                                  paddingTop: "5px",
                                  paddingLeft: "20px",
                                }}
                                onChange={this.handleChangeFixedPrice.bind(this, k)}
                              />
                            )
                            }
                          </Form.Item>
                        </Col>
                        <Col span={16}>
                          <Form.Item
                            {...formItemLayout}
                            label={<IntlMessages id="items.salesPrice" />}
                          >
                            {getFieldDecorator(`fixed_price_sales[${k}]`, {
                              initialValue: data.unit_conversion[k] ? parseFloat(data.unit_conversion[k].fixed_price_sales) : 0,
                              rules: [
                                {
                                  required: true,
                                  message: <IntlMessages id="required" />
                                }
                              ]
                            })(
                              <InputNumber
                                disabled={getFieldValue(`is_fixed_price[${k}]`) ? false : true}
                                formatter={value =>
                                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                                }
                                parser={value => value.replace(/\$\s?|(,*)/g, "")}
                                min={0}
                                precision={2}
                                style={{ width: "100%" }}
                              />
                            )}
                          </Form.Item>
                        </Col>
                      </InputGroup>
                    </Col>
                    <Col span={2} />
                    <Col span={10}>
                      <InputGroup compact>
                        <Col span={10}>
                          <Form.Item
                            {...formItemLayout}
                            label={<IntlMessages id="marginIn" />}
                          >
                            {getFieldDecorator(`margin_in[${k}]`, {
                              initialValue: data.unit_conversion[k] && data.unit_conversion[k].margin ? data.unit_conversion[k].margin.slice(-1) === "%"
                                ? 0
                                : 1
                                : 1,
                            })(
                              <Radio.Group
                                style={{ width: "100%" }}
                                buttonStyle="solid"
                                disabled={getFieldValue(`is_fixed_price[${k}]`) ? true : false}
                              >
                                <Radio.Button
                                  style={{ width: "50%", textAlign: "center" }}
                                  value={0}
                                >
                                  %
                              </Radio.Button>
                                <Radio.Button
                                  style={{ width: "50%", textAlign: "center" }}
                                  value={1}
                                >
                                  Rp
                              </Radio.Button>
                              </Radio.Group>
                            )}
                          </Form.Item>
                        </Col>
                        <Col span={14}>
                          {
                            getFieldValue(`margin_in[${k}]`) === 1
                              ?
                              (
                                <Form.Item
                                  {...formItemLayout}
                                  label={<IntlMessages id="items.margin" />}
                                >
                                  {getFieldDecorator(`margin[${k}]`, {
                                    initialValue: data.unit_conversion[k] ? parseFloat(data.unit_conversion[k].margin) : 0,
                                    rules: [
                                      {
                                        required: true,
                                        message: <IntlMessages id="required" />
                                      }
                                    ]
                                  })(
                                    <InputNumber
                                      disabled={getFieldValue(`is_fixed_price[${k}]`) ? true : false}
                                      formatter={value =>
                                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                                      }
                                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                                      min={0}
                                      precision={2}
                                      style={{ width: "100%" }}
                                    />
                                  )}
                                </Form.Item>
                              )
                              :
                              (
                                <Form.Item
                                  {...formItemLayout}
                                  label={<IntlMessages id="items.margin" />}
                                >
                                  {getFieldDecorator(`margin[${k}]`, {
                                    initialValue: data.unit_conversion[k] ? parseFloat(data.unit_conversion[k].margin) : 0,
                                    rules: [
                                      {
                                        required: true,
                                        message: <IntlMessages id="required" />
                                      }
                                    ]
                                  })(
                                    <InputNumber
                                      disabled={getFieldValue(`is_fixed_price[${k}]`) ? true : false}
                                      min={0}
                                      max={100}
                                      precision={2}
                                      formatter={value => `${value}%`}
                                      parser={value => value.replace("%", "")}
                                      style={{ width: "100%" }}
                                    />
                                  )}
                                </Form.Item>
                              )
                          }
                        </Col>
                      </InputGroup>
                    </Col>
                  </Col>
                )
                : null
              }

            </InputGroup>

            <InputGroup
            // style={{ 
            //   paddingLeft: "50px",
            //   paddingRight: "50px"
            // }}          
            >
              <Row>
                <Col span={3}>
                  <Form.Item
                    {...(index === 0 ? formItemLayout : formItemLayoutWithOutLabel)}
                    label={<IntlMessages id="items.weight" />}
                  //key={k}
                  >
                    {getFieldDecorator(`weight[${k}]`, {
                      initialValue: data.unit_conversion[k] ? parseFloat(data.unit_conversion[k].weight) : null,
                    })(
                      <InputNumber
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={0}
                        precision={2}
                        style={{ width: "95%" }}
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...(index === 0 ? formItemLayout : formItemLayoutWithOutLabel)}
                    label={<IntlMessages id="items.unitWeight" />}
                  //key={k}
                  >
                    {getFieldDecorator(`id_unit_weight[${k}]`, {
                      initialValue: data.unit_conversion[k] ? data.unit_conversion[k].id_unit_weight : null,
                    })(
                      <Select
                        loading={this.props.item.loading}
                        showSearch
                        style={{ width: "100%" }}
                        optionFilterProp="children"
                      >
                        {this.props.item.units.map((data, key) => (
                          (<Option key={key} value={data.id_unit}>
                            {data.unit_code + " (" + data.unit_name + ")"}
                          </Option>)
                        ))}

                      </Select>
                    )}
                  </Form.Item>
                </Col>
                <Col span={1} />

                <Col span={9}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="measureBy" />}
                  //key={k}
                  >
                    {getFieldDecorator(`measure_by[${k}]`, {
                      initialValue: data.unit_conversion[k] ? data.unit_conversion[k].measure_by : 1,
                    })(
                      <Radio.Group
                        buttonStyle="solid"
                        style={radioGroupStyle}
                      >
                        <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="noMeasure" /></Radio.Button>
                        <Radio.Button style={radioButtonStyle} value={2}><IntlMessages id="byVolume" /></Radio.Button>
                        <Radio.Button style={radioButtonStyle} value={3}><IntlMessages id="byDimension" /></Radio.Button>
                      </Radio.Group>

                    )}
                  </Form.Item>
                </Col>
              </Row>
              {
                getFieldValue(`measure_by[${k}]`) === 2
                  ?
                  (
                    <Row>
                      <Col span={3}>
                        <Form.Item
                          {...(index === 0 ? formItemLayout : formItemLayoutWithOutLabel)}
                          label={<IntlMessages id="items.volume" />}
                        //key={k}
                        >
                          {getFieldDecorator(`volume[${k}]`, {
                            initialValue: data.unit_conversion[k] ? parseFloat(data.unit_conversion[k].volume) : null,
                          })(
                            <InputNumber
                              formatter={value =>
                                `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                              }
                              parser={value => value.replace(/\$\s?|(,*)/g, "")}
                              min={0}
                              precision={2}
                              style={{ width: "95%" }}
                            />
                          )}
                        </Form.Item>
                      </Col>
                      <Col span={4}>
                        <Form.Item
                          {...(index === 0 ? formItemLayout : formItemLayoutWithOutLabel)}
                          label={<IntlMessages id="items.unitVolume" />}
                        //key={k}
                        >
                          {getFieldDecorator(`id_unit_volume[${k}]`, {
                            initialValue: data.unit_conversion[k] ? data.unit_conversion[k].id_unit_volume : null,
                          })(
                            <Select
                              loading={this.props.item.loading}
                              showSearch
                              style={{ width: "100%" }}
                              optionFilterProp="children"
                            >
                              {this.props.item.units.map((data, key) => (
                                (<Option key={key} value={data.id_unit}>
                                  {data.unit_code + " (" + data.unit_name + ")"}
                                </Option>)
                              ))}

                            </Select>
                          )}
                        </Form.Item>
                      </Col>
                    </Row>
                  )
                  :
                  getFieldValue(`measure_by[${k}]`) === 3
                    ?
                    (
                      <Row>
                        <Col span={3}>
                          <Form.Item
                            {...(index === 0 ? formItemLayout : formItemLayoutWithOutLabel)}
                            label={<IntlMessages id="items.length" />}
                          //key={k}
                          >
                            {getFieldDecorator(`length[${k}]`, {
                              initialValue: data.unit_conversion[k] ? parseFloat(data.unit_conversion[k].length) : null,
                            })(
                              <InputNumber
                                formatter={value =>
                                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                                }
                                parser={value => value.replace(/\$\s?|(,*)/g, "")}
                                min={0}
                                precision={2}
                                style={{ width: "95%" }}
                              />
                            )}
                          </Form.Item>
                        </Col>
                        <Col span={3}>
                          <Form.Item
                            {...(index === 0 ? formItemLayout : formItemLayoutWithOutLabel)}
                            label={<IntlMessages id="items.width" />}
                          //key={k}
                          >
                            {getFieldDecorator(`width[${k}]`, {
                              initialValue: data.unit_conversion[k] ? parseFloat(data.unit_conversion[k].width) : null,
                            })(
                              <InputNumber
                                formatter={value =>
                                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                                }
                                parser={value => value.replace(/\$\s?|(,*)/g, "")}
                                min={0}
                                precision={2}
                                style={{ width: "95%" }}
                              />
                            )}
                          </Form.Item>
                        </Col>
                        <Col span={3}>
                          <Form.Item
                            {...(index === 0 ? formItemLayout : formItemLayoutWithOutLabel)}
                            label={<IntlMessages id="items.height" />}
                          //key={k}
                          >
                            {getFieldDecorator(`height[${k}]`, {
                              initialValue: data.unit_conversion[k] ? parseFloat(data.unit_conversion[k].height) : null,
                            })(
                              <InputNumber
                                formatter={value =>
                                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                                }
                                parser={value => value.replace(/\$\s?|(,*)/g, "")}
                                min={0}
                                precision={2}
                                style={{ width: "95%" }}
                              />
                            )}
                          </Form.Item>
                        </Col>
                        <Col span={4}>
                          <Form.Item
                            {...(index === 0 ? formItemLayout : formItemLayoutWithOutLabel)}
                            label={<IntlMessages id="items.unitDimension" />}
                          //key={k}
                          >
                            {getFieldDecorator(`id_unit_dimension[${k}]`, {
                              initialValue: data.unit_conversion[k] ? data.unit_conversion[k].id_unit_dimension : null,
                            })(
                              <Select
                                loading={this.props.item.loading}
                                showSearch
                                style={{ width: "100%" }}
                                optionFilterProp="children"
                              >
                                {this.props.item.units.map((data, key) => (
                                  (<Option key={key} value={data.id_unit}>
                                    {data.unit_code + " (" + data.unit_name + ")"}
                                  </Option>)
                                ))}

                              </Select>
                            )}
                          </Form.Item>
                        </Col>
                      </Row>
                    )
                    : null
              }

              {data.unit_conversion[k]
                ? data.unit_conversion[k].is_used
                  ? null
                  :
                  (
                    <Row>
                      <Col span={11} />
                      <Col span={2}>
                        <Popconfirms
                          title={<IntlMessages id="confirmDelete" />}
                          onConfirm={() => this.removeUnit(k)}
                          okText={<IntlMessages id="yes" />}
                          cancelText={<IntlMessages id="no" />}
                        >
                          <Tooltip
                            title={<IntlMessages id="delete" />}
                          >
                            <Button
                              className="dynamic-delete-button"
                              icon="delete"
                              style={{ marginTop: "20px" }}
                              shape="circle"
                              type="danger"
                            />
                          </Tooltip>
                        </Popconfirms>
                      </Col>
                    </Row>
                  )
                :
                (
                  <Row>
                    <Col span={11} />
                    <Col span={2}>
                      <Popconfirms
                        title={<IntlMessages id="confirmDelete" />}
                        onConfirm={() => this.removeUnit(k)}
                        okText={<IntlMessages id="yes" />}
                        cancelText={<IntlMessages id="no" />}
                      >
                        <Tooltip
                          title={<IntlMessages id="delete" />}
                        >
                          <Button
                            className="dynamic-delete-button"
                            icon="delete"
                            style={{ marginTop: "20px" }}
                            shape="circle"
                            type="danger"
                          />
                        </Tooltip>
                      </Popconfirms>
                    </Col>
                  </Row>
                )

              }
            </InputGroup>
          </div>
        )
        : null
    ));

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const formItemLayoutWithOutLabel = {
      wrapperCol: {
        xs: { span: 24, offset: 0 },
        sm: { span: 20, offset: 4 },
      },
    };

    return (
      <div>
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          {used ? (
            <Row style={{ marginBottom: "20px" }}>
              <Col span={24}>
                <Alert
                  message={<IntlMessages id="items.help" />}
                  type="warning"
                  showIcon
                />
              </Col>
            </Row>
          ) : (
              ""
            )}
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.itemType" />}
              >
                {getFieldDecorator("item_type", {
                  initialValue: data.item_type,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    allowClear
                    disabled={used ? true : false}
                    filterOption
                    showSearch
                    style={{ width: "100%" }}
                    // placeholder={
                    //   <IntlMessages id="items.select.itemType" />
                    // }
                    optionFilterProp="children"
                  // loading={this.props.item.loading}
                  //showArrow={false}
                  >
                    <Option value={1}>Inventory</Option>
                    <Option value={2}>Non Inventory</Option>
                    <Option value={3}>Grouping</Option>
                    <Option value={4}>Service</Option>
                    {/* <Option value={5}>Asset</Option> */}
                  </Select>

                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemCategories.pageHeader" />}
              >
                {getFieldDecorator("id_item_category", {
                  initialValue: data.id_item_category,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <TreeSelect
                    // placeholder={
                    //   <IntlMessages id="items.select.itemCategory" />
                    // }
                    //onChange={this.handleFilterChange.bind(this, "id_item_category")}
                    style={{ width: "100%" }}
                    allowClear={true}
                    showSearch={true}
                    treeNodeFilterProp="title"
                    dropdownStyle={{ maxHeight: 300, overflow: 'auto' }}
                    treeData={loop(item_categories)}
                    treeDefaultExpandAll
                    loading={this.props.item.loading}
                  />
                )}
              </Form.Item>


              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.code" />}
                validateStatus={
                  error.item_code || getFieldError("item_code")
                    ? "error"
                    : null
                }
                help={
                  error.item_code
                    ? error.item_code
                    : getFieldError("item_code")
                }
              >
                {getFieldDecorator("item_code", {
                  initialValue: data.item_code,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 30,
                      message: <IntlMessages id="maxLength30" />
                    }
                  ]
                })(
                  <Input
                    disabled={used ? true : false}
                  />
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.name" />}
              >
                {getFieldDecorator("item_name", {
                  initialValue: data.item_name,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(
                  <Input
                    disabled={used ? true : false}
                    style={{ width: "100%" }}
                  />
                )}
              </Form.Item>



              {/* <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.warehouse" />}
              >
                {getFieldDecorator("id_warehouse_location", {
                  //initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.item.loading}
                    showSearch
                    optionFilterProp="children"
                    //disabled={used ? true : false}
                  >
                    {this.props.item.locations.map((data, key) => (
                      <Option key={key} value={data.id_location}>
                        {data.location_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item> */}


            </div>
            <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
              <InputGroup>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="colors.pageHeader" />}
                  >
                    {getFieldDecorator("id_color", {
                      initialValue: data.id_color,
                    })(
                      <Select
                        loading={this.props.item.loading}
                        showSearch
                        optionFilterProp="children"
                        //disabled={used ? true : false}
                        style={{ width: "100%" }}
                        allowClear={true}
                      >
                        {this.props.item.colors.map((data, key) => (
                          <Option key={key} value={data.id_color}>
                            {data.color_name}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="sizes.pageHeader" />}
                  >
                    {getFieldDecorator("id_size", {
                      initialValue: data.id_size,
                    })(
                      <Select
                        loading={this.props.item.loading}
                        showSearch
                        optionFilterProp="children"
                        //disabled={used ? true : false}
                        style={{ width: "100%" }}
                        allowClear={true}
                      >
                        {this.props.item.sizes.map((data, key) => (
                          <Option key={key} value={data.id_size}>
                            {data.size_code + " (" + data.size_name + ")"}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.barcode" />}
              >
                {getFieldDecorator("barcode", {
                  initialValue: data.barcode,
                  rules: [
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(
                  <Input
                    //   disabled={used ? true : false}
                    style={{ width: "100%" }}
                  />
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.sku" />}
              >
                {getFieldDecorator("sku", {
                  initialValue: data.sku,
                  rules: [
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(
                  <Input
                    //   disabled={used ? true : false}
                    style={{ width: "100%" }}
                  />
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.skuName" />}
              >
                {getFieldDecorator("sku_name", {
                  initialValue: data.sku_name,
                  rules: [
                    {
                      max: 200,
                      message: <IntlMessages id="maxLength200" />
                    }
                  ]
                })(
                  <Input
                    //   disabled={used ? true : false}
                    style={{ width: "100%" }}
                  />
                )}
              </Form.Item>

              <Form.Item
                {...formTextArea}
                label={<IntlMessages id="locations.add.description" />}
              >
                {getFieldDecorator("description", {
                  initialValue: data.description,
                })(<TextArea
                  autosize={{ minRows: 1 }}
                />)}
              </Form.Item>

            </div>
          </div>

          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="itemUnit" />
          </Divider>

          <InputGroup
          // style={{ 
          //   paddingLeft: "50px",
          //   paddingRight: "50px"
          // }}
          >
            <Col span={10}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.baseUnit" />}
              >
                {getFieldDecorator("id_base_unit", {
                  initialValue: data.id_base_unit,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.item.loading}
                    showSearch
                    onChange={this.handleChangeBaseUnit}
                    optionFilterProp="children"
                    disabled={used ? true : false}
                    style={{ width: "100%" }}
                  >
                    {this.props.item.units.map((data, key) => (
                      <Option key={key} value={data.id_unit}>
                        {data.unit_code + " (" + data.unit_name + ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>

            {getFieldValue('item_type') !== 2
              ?
              (
                <Col span={14}>
                  <Col span={1} />
                  <Col span={10}>
                    <InputGroup compact>
                      <Col span={8}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="isFixedPrice" />}
                        >
                          {getFieldDecorator("base_unit_is_fixed_price", {
                            valuePropName: 'checked',
                            initialValue: data.unit_conversion
                              ? data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).is_fixed_price === 1
                                ? true
                                : false
                              : false,
                          })(
                            <Checkbox
                              style={{
                                paddingTop: "5px",
                                paddingLeft: "20px",
                              }}
                              onChange={this.handleChangeBaseUnitFixedPrice}
                            />
                          )
                          }
                        </Form.Item>
                      </Col>
                      <Col span={16}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="items.salesPrice" />}
                        >
                          {getFieldDecorator("base_unit_fixed_price_sales", {
                            initialValue: data.unit_conversion
                              ? parseFloat(data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).fixed_price_sales)
                              : 0,
                            rules: [
                              {
                                required: true,
                                message: <IntlMessages id="required" />
                              }
                            ]
                          })(
                            <InputNumber
                              disabled={getFieldValue('base_unit_is_fixed_price') ? false : true}
                              formatter={value =>
                                `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                              }
                              parser={value => value.replace(/\$\s?|(,*)/g, "")}
                              min={0}
                              precision={2}
                              style={{ width: "100%" }}
                            />
                          )}
                        </Form.Item>
                      </Col>
                    </InputGroup>
                  </Col>
                  <Col span={2} />
                  <Col span={10}>
                    <InputGroup compact>
                      <Col span={10}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="marginIn" />}
                        >
                          {getFieldDecorator("base_unit_margin_in", {
                            initialValue: data.unit_conversion
                              ? data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).margin.slice(-1) === "%"
                                ? 0
                                : 1
                              : 1,
                          })(
                            <Radio.Group
                              style={{ width: "100%" }}
                              buttonStyle="solid"
                              disabled={getFieldValue('base_unit_is_fixed_price') ? true : false}
                            >
                              <Radio.Button
                                style={{ width: "50%", textAlign: "center" }}
                                value={0}
                              >
                                %
                            </Radio.Button>
                              <Radio.Button
                                style={{ width: "50%", textAlign: "center" }}
                                value={1}
                              >
                                Rp
                            </Radio.Button>
                            </Radio.Group>
                          )}
                        </Form.Item>
                      </Col>
                      <Col span={14}>
                        {
                          getFieldValue('base_unit_margin_in') === 1
                            ?
                            (
                              <Form.Item
                                {...formItemLayout}
                                label={<IntlMessages id="items.margin" />}
                              >
                                {getFieldDecorator("base_unit_margin", {
                                  initialValue: data.unit_conversion
                                    ? parseFloat(data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).margin)
                                    : 0,
                                  rules: [
                                    {
                                      required: true,
                                      message: <IntlMessages id="required" />
                                    }
                                  ]
                                })(
                                  <InputNumber
                                    disabled={getFieldValue('base_unit_is_fixed_price') ? true : false}
                                    formatter={value =>
                                      `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                                    }
                                    parser={value => value.replace(/\$\s?|(,*)/g, "")}
                                    min={0}
                                    precision={2}
                                    style={{ width: "100%" }}
                                  />
                                )}
                              </Form.Item>
                            )
                            :
                            (
                              <Form.Item
                                {...formItemLayout}
                                label={<IntlMessages id="items.margin" />}
                              >
                                {getFieldDecorator("base_unit_margin", {
                                  initialValue: data.unit_conversion
                                    ? parseFloat(data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).margin)
                                    : 0,
                                  rules: [
                                    {
                                      required: true,
                                      message: <IntlMessages id="required" />
                                    }
                                  ]
                                })(
                                  <InputNumber
                                    disabled={getFieldValue('base_unit_is_fixed_price') ? true : false}
                                    min={0}
                                    max={100}
                                    precision={2}
                                    formatter={value => `${value}%`}
                                    parser={value => value.replace("%", "")}
                                    style={{ width: "100%" }}
                                  />
                                )}
                              </Form.Item>
                            )
                        }
                      </Col>
                    </InputGroup>
                  </Col>
                </Col>
              )
              : null
            }

            {/* <Col span={1} /> */}
          </InputGroup>

          <InputGroup
          // style={{ 
          //   paddingLeft: "50px",
          //   paddingRight: "50px"
          // }}
          >
            <Row>
              <Col span={3}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="items.weight" />}
                >
                  {getFieldDecorator('base_unit_weight', {
                    initialValue: data.unit_conversion
                      ? parseFloat(data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).weight)
                      : null
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={0}
                      precision={2}
                      style={{ width: "95%" }}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={4}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="items.unitWeight" />}
                >
                  {getFieldDecorator('id_base_unit_weight', {
                    initialValue: data.unit_conversion
                      ? data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).id_unit_weight
                      : null
                  })(
                    <Select
                      loading={this.props.item.loading}
                      showSearch
                      style={{ width: "100%" }}
                      optionFilterProp="children"
                    >
                      {this.props.item.units.map((data, key) => (
                        (<Option key={key} value={data.id_unit}>
                          {data.unit_code + " (" + data.unit_name + ")"}
                        </Option>)
                      ))}

                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={1} />
              <Col span={9}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="measureBy" />}
                >
                  {getFieldDecorator('base_unit_measure_by', {
                    initialValue: data.unit_conversion
                      ? data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).measure_by
                      : null
                  })(
                    <Radio.Group
                      buttonStyle="solid"
                      style={radioGroupStyle}
                    >
                      <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="noMeasure" /></Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={2}><IntlMessages id="byVolume" /></Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={3}><IntlMessages id="byDimension" /></Radio.Button>
                    </Radio.Group>

                  )}
                </Form.Item>
              </Col>
            </Row>

            {
              getFieldValue('base_unit_measure_by') === 2
                ?
                (
                  <Row>
                    <Col span={3}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.volume" />}
                      >
                        {getFieldDecorator('base_unit_volume', {
                          initialValue: data.unit_conversion
                            ? parseFloat(data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).volume)
                            : null
                        })(
                          <InputNumber
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            min={0}
                            precision={2}
                            style={{ width: "95%" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={4}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.unitVolume" />}
                      >
                        {getFieldDecorator('id_base_unit_volume', {
                          initialValue: data.unit_conversion
                            ? data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).id_unit_volume
                            : null
                        })(
                          <Select
                            loading={this.props.item.loading}
                            showSearch
                            style={{ width: "100%" }}
                            optionFilterProp="children"
                          >
                            {this.props.item.units.map((data, key) => (
                              (<Option key={key} value={data.id_unit}>
                                {data.unit_code + " (" + data.unit_name + ")"}
                              </Option>)
                            ))}

                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  </Row>
                )
                :
                getFieldValue('base_unit_measure_by') === 3
                  ?
                  (
                    <Row>
                      <Col span={3}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="items.length" />}
                        >
                          {getFieldDecorator('base_unit_length', {
                            initialValue: data.unit_conversion
                              ? data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).length
                              : null
                          })(
                            <InputNumber
                              formatter={value =>
                                `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                              }
                              parser={value => value.replace(/\$\s?|(,*)/g, "")}
                              min={0}
                              precision={2}
                              style={{ width: "95%" }}
                            />
                          )}
                        </Form.Item>
                      </Col>
                      <Col span={3}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="items.width" />}
                        >
                          {getFieldDecorator('base_unit_width', {
                            initialValue: data.unit_conversion
                              ? parseFloat(data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).width)
                              : null
                          })(
                            <InputNumber
                              formatter={value =>
                                `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                              }
                              parser={value => value.replace(/\$\s?|(,*)/g, "")}
                              min={0}
                              precision={2}
                              style={{ width: "95%" }}
                            />
                          )}
                        </Form.Item>
                      </Col>
                      <Col span={3}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="items.height" />}
                        >
                          {getFieldDecorator('base_unit_height', {
                            initialValue: data.unit_conversion
                              ? parseFloat(data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).height)
                              : null
                          })(
                            <InputNumber
                              formatter={value =>
                                `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                              }
                              parser={value => value.replace(/\$\s?|(,*)/g, "")}
                              min={0}
                              precision={2}
                              style={{ width: "95%" }}
                            />
                          )}
                        </Form.Item>
                      </Col>
                      <Col span={4}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="items.unitDimension" />}
                        >
                          {getFieldDecorator('id_base_unit_dimension', {
                            initialValue: data.unit_conversion
                              ? data.unit_conversion.find((unit) => unit.id_unit === data.id_base_unit).id_unit_dimension
                              : null
                          })(
                            <Select
                              loading={this.props.item.loading}
                              showSearch
                              style={{ width: "100%" }}
                              optionFilterProp="children"
                            >
                              {this.props.item.units.map((data, key) => (
                                (<Option key={key} value={data.id_unit}>
                                  {data.unit_code + " (" + data.unit_name + ")"}
                                </Option>)
                              ))}

                            </Select>
                          )}
                        </Form.Item>
                      </Col>
                    </Row>
                  )
                  : null
            }
          </InputGroup>

          {formItems}

          <Divider />

          {
            keys.length < 5
              ?
              (
                <Form.Item {...formItemLayoutWithOutLabel}>
                  <Button type="dashed" onClick={this.addUnit} style={{ width: '60%' }}>
                    <Icon type="plus" />
                    <IntlMessages id="button.addUnitConversion" />
                  </Button>
                </Form.Item>
              )
              : null
          }

          <Row>
            <Col>
              <Form.Item {...tailFormItemLayout}>
                <Button
                  type="primary"
                  htmlType="submit"
                  style={{ marginTop: "20px", marginLeft: "50px" }}
                  loading={loading || loadingSubmit}
                >
                  <IntlMessages id="button.submit" />
                </Button>
              </Form.Item>
            </Col>
          </Row>


        </Form>
      </div>
    );
  }
}

const WrappedItemUpdateDescForm = Form.create({
  name: "update_item_desc"
})(ItemUpdateDescForm);

//export default WrappedItemUpdateDescForm;

const mapStateToProps = state => {
  return { item: state.item };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedItemUpdateDescForm);
