import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Icon,Row, Col, InputNumber, Select, Spin, Tooltip } from "antd";
import actions from "../../../../redux/item/item/actions";
// import { updateItemGroup } from "../../../../redux/item/item/saga";
import Popconfirms from "../../../../components/feedback/popconfirm";

const { Option } = Select;
// const { TextArea } = Input;
const InputGroup = Input.Group;

let id=0;

class ItemUpdateGroupForm extends Component {
  state = {
  }

  // componentDidMount() {
  //   const { editItem } = this.props;
  //   const id  = this.props.id_item;
  //   editItem(id);
  // }

  componentDidMount() {
    if (this.props.item.item!==null) {
      // console.log(this.props.item.item);
      let child_item_group = this.props.item.item.child_item_group;
      let array = [];
      const { form } = this.props

      child_item_group.map((data, index) => {
        array.push(index);
      })
      form.setFieldsValue({
        keys: array,
      });
      // id=0
      id=child_item_group.length 
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.item !== this.props.item && this.props.item.item!==null) {
      let child_item_group = this.props.item.item.child_item_group;
      let array = [];
      const { form } = this.props

      child_item_group.map((data, index) => {
        array.push(index);
      })
      form.setFieldsValue({
        keys: array,
      });

      // id=0
      id=child_item_group.length 
    }
  }

  removeChild = (k) => {
    const { form } = this.props;
    // can use data-binding to get
    const keys = form.getFieldValue('keys');

    // can use data-binding to set
    form.setFieldsValue({
      keys: keys.filter(key => key !== k),
      [`id_item_child[${k}]`]: undefined
    });
  }

  addChild = () => {
    const { form } = this.props;
    // can use data-binding to get
    const keys = form.getFieldValue('keys');
    
    const nextKeys = keys.concat(id++);
    // can use data-binding to set
    // important! notify form to detect changes
    form.setFieldsValue({
      keys: nextKeys,
    });
  }

  handleChangeCompositionItem = (index, e) => {
    // console.log(e)
    // console.log(index)
    const {setFieldsValue} = this.props.form

    this.props.item.inventory_items.map((data) => {
      if(e === data.id_item){
        // console.log(data.base_unit.unit_name)
        setFieldsValue ({
          [`unit_name[${index}]`] : data.base_unit.unit_name 
        })
      } 
    })
  }

  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { form } = this.props;
      const keys = form.getFieldValue('keys');
      let item_groups = [];

      if(keys.length>0){
        values.id_item_child.map((data, index) => {
          item_groups.push({
                id_item_child: values.id_item_child[index],
                quantity: values.quantity[index]
              })
        })
      }

      values.item_groups = item_groups

      //console.log(values);
      if (!err) {
        // console.log(values)
        const { updateItemGroup } = this.props;
        updateItemGroup(this.props.item.item.id_item, values);
        // this.props.submit(values);
      }
    });
  }

  render() {
    // console.log(id);
    // console.log(this.props.item.item);
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { item } = this.props;
    const { loading, loadingSubmit } = item;
    const data = item.item ? item.item : [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    // let id_item_child = getFieldValue('id_item_child') ? getFieldValue('id_item_child') : []

    // const used = data.is_used;
    // const error = item.errorField ? item.errorField : [];
    // console.log(data);

    getFieldDecorator('keys', { initialValue: [] });
    const keys = getFieldValue('keys');
    const formItems = keys.map((k, index) => (
      //console.log(data.unit_conversion[k])
      <InputGroup
        key={k}
      >
        <Col span={10}>  
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="items.itemChild" />}
            //required={false}
            key={k}
          >
            {getFieldDecorator(`id_item_child[${k}]`, {
              //validateTrigger: ['onChange', 'onBlur'],
              initialValue: data.child_item_group[k] ? data.child_item_group[k].id_item_child : null,
              rules: [{
                required: true,
                //whitespace: true,
                message: <IntlMessages id="required" />,
              }],
            })(
              <Select
                  loading={this.props.item.loading}
                  showSearch
                  style={{ width:"100%" }}
                  onChange={this.handleChangeCompositionItem.bind(this, k)}
                  optionFilterProp="children"
                >
                  {this.props.item.inventory_items.map((data, key) => (
                    getFieldValue(`id_item_child[${k}]`) === data.id_item 
                      ? (
                          <Option key={key} value={data.id_item}>
                            {data.item_name}
                          </Option>
                        )
                      : Array.isArray(getFieldValue('id_item_child')) && 
                      !getFieldValue('id_item_child').includes(data.id_item)
                        ? (
                            <Option key={key} value={data.id_item}>
                              {data.item_name}
                            </Option>
                          )
                        : null
                  ))}
                </Select>
            )}
          
          </Form.Item>
        </Col>
        <Col span={6}>  
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="unit" />}
            //required={false}
            key={k}
          >
            {getFieldDecorator(`unit_name[${k}]`, {
              //validateTrigger: ['onChange', 'onBlur'],
              initialValue: data.child_item_group[k] ? data.child_item_group[k].child_item.base_unit.unit_name : null,
              // rules: [{
              //   required: true,
              //   //whitespace: true,
              //   message: <IntlMessages id="required" />,
              // }],
            })(
              <Input
                  style={{ width:"100%" }}
                  // readOnly={true}
                  disabled={true}
                />
            )}
          
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="quantity" />}
            //required={false}
            key={k}
          >
            {getFieldDecorator(`quantity[${k}]`, {
              //validateTrigger: ['onChange', 'onBlur'],
              initialValue: data.child_item_group[k] ? data.child_item_group[k].quantity : null,
              rules: [{
                required: true,
                //whitespace: true,
                message: <IntlMessages id="required" />,
              }],
            })(
              <InputNumber
                formatter={value =>
                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                }
                parser={value => value.replace(/\$\s?|(,*)/g, "")}
                min={0}
                style={{ width: '100%' }}
              />
            )}  
          </Form.Item>
        </Col>
        <Col span={2}>
          <Popconfirms
            title={<IntlMessages id="confirmDelete" />}
            onConfirm={() => this.removeChild(k)}
            okText={<IntlMessages id="yes" />}
            cancelText={<IntlMessages id="no" />}
          >
            <Tooltip
              title={<IntlMessages id="delete" />}
            >
              <Button
                className="dynamic-delete-button"
                icon="delete"
                style={{ marginTop:"20px"}}
                shape="circle"
                type="danger"
                disabled={getFieldValue("keys").length === 1}
              />
            </Tooltip>
          </Popconfirms>
        </Col>         
      </InputGroup>
    ));

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const formItemLayoutWithOutLabel = {
      wrapperCol: {
        xs: { span: 24, offset: 0 },
        sm: { span: 20, offset: 4 },
      },
    };


    return (
      <div>
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            {/* <div className="isoColumn-2 isoFormGroupComp"> */}
            <Col span={18}>
              
              {formItems}

              <Form.Item {...formItemLayoutWithOutLabel}>
                <Button type="dashed" onClick={this.addChild} style={{ width: '60%' }}>
                  <Icon type="plus" /> {<IntlMessages id="addItemChild" />}
                </Button>
              </Form.Item>
            </Col>  
            {/* </div> */}
          </div>

          <Row>
            <Col>
            <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              htmlType="submit"
              style={{ marginTop: "20px", marginLeft: "50px" }}
              loading={loading || loadingSubmit}
              disabled={permissions.includes("Update Item Grouping") ? false : true}
            >
              <IntlMessages id="button.submit" />
            </Button>
          </Form.Item>
            </Col>
          </Row>

          
        </Form>
      </div>
    );
  }
}

const WrappedItemUpdateGroupForm = Form.create({
  name: "update_item_desc"
})(ItemUpdateGroupForm);

//export default WrappedItemUpdateGroupForm;

const mapStateToProps = state => {
  return { 
    item: state.item,
    auth: state.auth
   };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedItemUpdateGroupForm);
