import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import ItemUpdateDescForm from "./updateDescForm";
//import ItemUpdateSalesPurchaseForm from "./updateSalesPurchaseForm";
import ItemUpdateGroupForm from "./updateGroupForm"
import ItemUpdateAccountForm from "./updateAccountForm";
import ItemLocation from "./indexItemLocation";

import { Tabs, Spin } from "antd";

import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Box } from "../../../App/app.style";
import actions from "../../../../redux/item/item/actions";


const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/items";

class UpdateItem extends Component {
  state = {
    id_item: null,
    url: null,
  }

  componentWillMount = () => {
    this.setState({
      id_item: this.props.match.params.id,
      url: this.props.match.url,
    })
  }

  componentDidMount() {
    const { editItem } = this.props;
    const id = this.props.match.params.id;
    editItem(id);
  }


  handleSubmitAddItemLocation = value => {
    const { storeItemLocation } = this.props;
    const { id } = this.props.match.params;
    storeItemLocation(id, value)
  }

  handleSubmitUpdateItemLocation = value => {
    const { updateItemLocation } = this.props;
    const { id } = this.props.match.params;
    updateItemLocation(id, value.id_location, value)
  }

  render() {
    const item = this.props.item.item ? this.props.item.item : []

    //Display error notif
    if (this.props.item.error) {
      notification("error", this.props.item.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.item.success) {
      notification("success", this.props.item.success);
      this.props.clearSuccess();
    }
    //console.log(this.props.match);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="items.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="items.tab.description" />}
              key="1"
            >
              {this.props.item.loading ?
                (
                  <div className="spinStyle">
                    <Spin size="large" />
                  </div>
                )
                :
                (
                  <ItemUpdateDescForm
                    auth={this.props.auth}
                    id_item={this.state.id_item}
                    url={this.state.url}
                  />
                )
              }

            </TabPane>
            {/* <TabPane tab={<IntlMessages id="items.tab.salesPurchase" />} 
              key="2"
            > 
              <ItemUpdateSalesPurchaseForm
                id_item={this.state.id_item}
                url={this.state.url}
              />
            </TabPane> */}
            {item.item_type === 3
              ?
              (
                <TabPane tab={<IntlMessages id="items.tab.group" />}
                  key="3"
                >
                  {this.props.item.loading ?
                    (
                      <div className="spinStyle">
                        <Spin size="large" />
                      </div>
                    )
                    :
                    (
                      <ItemUpdateGroupForm
                        auth={this.props.auth}
                        id_item={this.state.id_item}
                        url={this.state.url}
                      />
                    )
                  }

                </TabPane>)
              : null
            }
            <TabPane tab={<IntlMessages id="items.tab.account" />}
              key="4"
            >
              {this.props.item.loading ?
                (
                  <div className="spinStyle">
                    <Spin size="large" />
                  </div>
                )
                :
                (
                  <ItemUpdateAccountForm
                    auth={this.props.auth}
                    id_item={this.state.id_item}
                    url={this.state.url}
                  />
                )
              }

            </TabPane>
            <TabPane tab={<IntlMessages id="items.tab.inventory" />}
              key="5"
            >
              {this.props.item.loading ?
                (
                  <div className="spinStyle">
                    <Spin size="large" />
                  </div>
                )
                :
                (
                  <ItemLocation
                    auth={this.props.auth}
                    id_item={this.state.id_item}
                    handleSubmitAddItemLocation={this.handleSubmitAddItemLocation}
                    handleSubmitUpdateItemLocation={this.handleSubmitUpdateItemLocation}
                    url={this.state.url}
                  />
                )
              }
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    item: state.item
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateItem);
