import React, { Component } from "react";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Modal, Radio, Col, Divider, InputNumber, Checkbox } from "antd";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class ModalAddItemLocation extends Component {
  state = {
    check_fixed_price:false,
    id_location: [],
    id_location_error : null,
    limit: 0,
  }

  handleChangeItemLocation = (e) => {
    //console.log(e);
    if(this.state.id_location_error!==null){
      this.setState({
        id_location_error: null
      })
    }

    if(e.includes("all")){
      const { item } = this.props;
      console.log("");    

      let available_locations = item.array_id_location.filter((data) => {
          if(!item.item.inventories.find((x) => x.id_location === data)){
            return data
          }
        }
      )      
      this.setState({
        id_location: available_locations
      })
    }
    else{
      const { setFieldsValue } = this.props.form;
      if(e.length===0){
        this.setState({
          id_location_error: "Required"
        })
      }
      console.log("");
      this.setState({
        id_location: e
      })
      

      if(e.length!==1){
        setFieldsValue({
          id_rack: [],
          id_supplier_preferred: null
        })
      }
    }
    
  }
  

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      let arr = this.state.id_location 
      if(arr.length===0){
        this.setState({
          id_location_error: "Required"
        })
      }
      else if (!err) {
        values.id_location = this.state.id_location
        //console.log(values);
        this.props.submit(values);
      }

    });
  };

  render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { item } = this.props;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="addItemLocation" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="60%"
        okText={<IntlMessages id="button.submit"></IntlMessages>}
        cancelText={<IntlMessages id="button.cancel"></IntlMessages>}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <InputGroup>
            <Col span={1} />
            <Col span={10}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.locations" />}
                required
                validateStatus={
                  this.state.id_location_error 
                    ? "error"
                    : null
                }
                help={
                  this.state.id_location_error
                }
              > 
                  
                <Select
                  mode="multiple"
                  loading={this.props.item.loading}
                  showSearch
                  optionFilterProp="children"
                  style={{ width:"100%" }}
                  onChange={this.handleChangeItemLocation.bind(this)}
                  value={this.state.id_location}
                  allowClear={true}
                >
                  {
                    item.item ?
                      item.locations.length - item.item.inventories.length > 0 
                      ? 
                        (
                          <Option key={-1} value={"all"}>
                              {<IntlMessages id="select.all.location" />}
                          </Option>
                        )
                      : null
                    : null
                  }     

                  {item.locations.map((data, key) => (
                    item.item !== null 
                    ?
                      item.item.inventories.filter((x) => x.id_location===data.id_location).length>0
                      ? null
                      : (<Option key={key} value={data.id_location}>
                          { data.location_code + " (" + data.location_name +")"}
                        </Option>)
                    : null  
                  ))}
                </Select>
              </Form.Item>
            </Col>
            <Col span={2} />
            <Col span={10}>
              {
                item.item !== null && (item.item.item_type === 3 || item.item.item_type === 4)
                  ? null
                  :  
                    (
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.preferredSupplier" />}
                      >
                        {getFieldDecorator("id_supplier_preferred", {
                          // initialValue: data.id_supplier_preferred,
                        })(
                          <Select
                            loading={this.props.item.loading}
                            showSearch
                            optionFilterProp="children"
                            style={{ width:"100%" }}
                          >
                            {item.suppliers.map((data, key) => (
                              this.state.id_location.length === 1 && 
                              data.id_location === this.state.id_location[0] 
                              ? 
                                (
                                  <Option key={key} value={data.id_supplier}>
                                    { data.supplier_code + " ("+data.supplier_name +")"}
                                  </Option>
                                )
                              : null   
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    )
              }
              
            </Col>
            <Col span={1} />
          </InputGroup>

          <Divider 
            ////orientation="left" 
            style={{ fontSize: 14 }}
          >
            <IntlMessages id="salesPurchaseItemLocation" />
          </Divider>

          <InputGroup>
            <Col span={1} />
            <Col span={10}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.purchaseUnit" />}
              >
                {getFieldDecorator("id_purchase_unit", {
                  //initialValue: data.id_purchase_unit,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.item.loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width: "100%" }}
                  >
                    {item.units.map((data, key) => (
                      item.item !== null 
                      ?
                        item.item.array_unit_conversion.filter((value)=>value===data.id_unit).length > 0
                          ? 
                          (<Option key={key} value={data.id_unit}>
                            { data.unit_code +" ("+data.unit_name+")"}
                          </Option>)
                        : null
                      : null
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={2} />
            {
              item.item !== null && (item.item.item_type !== 2)
                ? 
                  (
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.salesUnit" />}
                      >
                        {getFieldDecorator("id_sales_unit", {
                          //initialValue: data.id_sales_unit,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            loading={this.props.item.loading}
                            showSearch
                            optionFilterProp="children"
                            style={{ width: "100%" }}
                          >
                            {item.units.map((data, key) => (
                              item.item !== null 
                              ?
                                item.item.array_unit_conversion.filter((value)=>value===data.id_unit).length > 0
                                  ? 
                                  (<Option key={key} value={data.id_unit}>
                                    { data.unit_code +" ("+data.unit_name+")"}
                                  </Option>)
                                : null
                              : null
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  )
                : null
            }
            <Col span={1} />
          </InputGroup>

          <InputGroup>
            <Col span={1} />
            <Col span={10}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.purchaseTax" />}
              >
                {getFieldDecorator("id_purchase_tax", {
                  //initialValue: data.id_purchase_tax,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={item.loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width: "100%" }}
                  >
                    {item.taxes.map((data, key) => (
                      <Option key={key} value={data.id_tax}>
                        { data.tax_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={2} />
            {
              item.item !== null && (item.item.item_type !== 2)
                ?
                  (
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.salesTax" />}
                      >
                        {getFieldDecorator("id_sales_tax", {
                          //initialValue: data.id_sales_tax,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            loading={item.loading}
                            showSearch
                            optionFilterProp="children"
                            style={{ width: "100%" }}
                          >
                            {item.taxes.map((data, key) => (
                              <Option key={key} value={data.id_tax}>
                                { data.tax_name}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  )
                : null
            }
              
            <Col span={1} />
          </InputGroup>

          <InputGroup>
            <Col span={1} />
            <Col span={10}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.purchaseQuantity" />}
              >
                {getFieldDecorator("purchase_qty", {
                  //initialValue: data.purchase_qty,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <InputNumber
                    formatter={value =>
                      `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    }
                    parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    min={0.01}
                    precision={2}
                    style={{ width: "100%" }}
                  />
                )}
              </Form.Item>
            </Col>
            <Col span={2} />
            <Col span={10}>
              
            </Col>
            <Col span={1} />
          </InputGroup>

          {
            item.item !== null && (item.item.item_type !== 1)
            ? null
            : 
              (
                <>
                  <Divider 
                    //orientation="left" 
                    style={{ fontSize: 14 }}
                  >
                    <IntlMessages id="inventoryItemLocation" />
                  </Divider>
                  
                  <InputGroup>
                    <Col span={1} />
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.minStock" />}
                      >
                        {getFieldDecorator("min_stock", {
                          // initialValue: data.min_stock,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <InputNumber
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            min={0}
                            precision={2}
                            style={{ width: "100%" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={2} />
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.maxStock" />}
                      >
                        {getFieldDecorator("max_stock", {
                          // initialValue: data.max_stock,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <InputNumber
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            min={0}
                            precision={2}
                            style={{ width: "100%" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>  
                    
                  <InputGroup>
                    <Col span={1} />
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.reorderPoint" />}
                      >
                        {getFieldDecorator("reorder_point", {
                          // initialValue: data.reorder_point,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <InputNumber
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            min={0}
                            precision={2}
                            style={{ width: "100%" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={2} />
                    <Col span={10}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="items.rack" />}
                      >
                        {getFieldDecorator("id_rack", {
                          // initialValue: data.id_supplier_preferred,
                        })(
                          <Select
                            mode="multiple"
                            loading={this.props.item.loading}
                            showSearch
                            optionFilterProp="children"
                            style={{ width: "100%" }}
                          >
                            { item.racks.map((data, key) => (
                              this.state.id_location.length === 1 && 
                              data.id_location === this.state.id_location[0]
                                ? 
                                  (<Option key={key} value={data.id_rack}>
                                    { data.rack_code + " (" + data.rack_name +")"}
                                  </Option>)
                                : null
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={1} />
                  </InputGroup>
                </>
              )
          }

               
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddItemLocation = Form.create({ name: "modal_add_item_location" })(
  ModalAddItemLocation
);

export default WrappedModalAddItemLocation;
